import "../Style/TourBlockDetail.scss"
import TimeDetail from './TimeDetail'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faXmark,  faHeart as like, faSpinner  } from '@fortawesome/free-solid-svg-icons'
import { faHeart as dislike } from '@fortawesome/free-regular-svg-icons'
import { useState, useEffect } from 'react';
import { getFetch, setStateArrValueByIndex } from "../CommonFunction/commonFunction.js" 
import { useDispatch } from 'react-redux';
import { notShowBlockDetail } from '../features/handleBoolean/toggleBlock.js';



export default function TourBlockDetail({data, likeHeartCount, setLikeHeartCount}) {
  const [likeHeart, setLikeHeart] = useState(false);
  const dispatch = useDispatch();
    const tagsSplit = data["tags"].split("#");
    const [timeDetail, setTimeDetail] = useState([]);
    const [mainImg, setMainImg] = useState();


    useEffect(() => {
      fetchMainData();
      if(Array.isArray(data["file"])) setMainImg(data["file"][0]);
      else setMainImg(data["file"]);
      const cachedUserData = localStorage.getItem(data["id"]+"_like");
      if(cachedUserData) setLikeHeart(true);
  }, []);
    
    const fetchMainData = async() =>{
      let dataArr = Object.values(data["_subtable_1000139"]);
      dataArr.sort((a, b) => {
        // 将时间字符串转换为分钟数
        const timeA = parseInt(a.time.substring(0, 2)) * 60 + parseInt(a.time.substring(3));
        const timeB = parseInt(b.time.substring(0, 2)) * 60 + parseInt(b.time.substring(3));
        
        // 比较时间大小
        return timeA - timeB;
      });
      setTimeDetail(dataArr);
      // try{
      //   getFetch("https://ap9.ragic.com/js1031222/tour/3?api&v=3&order=1000134,ASC&where=1000138,eq,"+data.id).then(result=>{
      //     for(let item in result){
      //       let seq = result[item]["_seq"];
      //       setStateArrValueByIndex(seq, result[item], setTimeDetail);
      //     } 
      //   })
      // }catch(error){
      //   console.log("error: "+error);
      // }
    }

    const removeDetail = () =>{
        document.querySelector("html").style.overflowY = "auto";
        dispatch(notShowBlockDetail(data["id"]));
      }

      const handleLikeClick = (e) => {
        let currentLikeCount = likeHeartCount;
        e.stopPropagation();
        if(likeHeart){
            localStorage.removeItem(data["id"]+"_like");
            setLikeHeartCount(preState => preState-1);
            currentLikeCount--;
        } 
        else{
            localStorage.setItem(data["id"]+"_like", 1);
            setLikeHeartCount(preState => preState+1);
            currentLikeCount++;
        }
        setLikeHeart(preState => !preState);
    
        const submitFormData = new FormData();
        submitFormData.append("1000140", currentLikeCount);
        fetch("https://ap9.ragic.com/js1031222/tour/2/"+data["id"]+"?api&v=3", {
            "method":"post",
            "headers":new Headers({
                "Authorization": "Basic aHdkVEdYblovMzY4aWk0bDlXVEhyakJJOE9NSjU1T0tnaktWYnJ5elpNS2FLTjU2QWNwUGdweFhGalRBVVFxcw=="
            }),
            "body": submitFormData
        }).then(res => res.json())
        .then(data => {
        })
      }
  return (
    <>
        <div className='tourBlockDetail'>
          <div className='tourBlockDetailContainer'>
            <div className="tourBlockTitleContainer">
              <div className='detailTitle'>
                  <p>{data["name"]}</p>
              </div>
            </div>
            <div className='tagContainer'>
                {tagsSplit.map(item=>{
                    if(item){
                        return <div className='tagItem'>{item}</div>
                    }
                 })}
            </div>
            <div className="imgContainer">
              {mainImg?<img src={"https://ap9.ragic.com/sims/file.jsp?a=js1031222&f="+mainImg} alt="Tour Photo"></img>:""}
            </div>
            <div className="moreImgContainer">
             {Array.isArray(data["file"])? 
                data["file"].map(item=>{
                    return <img onClick={()=>{setMainImg(item)}}  src={"https://ap9.ragic.com/sims/file.jsp?a=js1031222&f="+item} alt='Tour Photo'></img>
                })
                :
                <img  src={"https://ap9.ragic.com/sims/file.jsp?a=js1031222&f="+data["file"]} alt='Tour Photo'></img>
              }
            </div>
            <div className='tourSchedule'>
              <span>行程</span>
              {likeHeart? 
              <FontAwesomeIcon onClick={handleLikeClick} icon={like} className="navIcons" />:
              <FontAwesomeIcon onClick={handleLikeClick} icon={dislike} className="navIcons" />
              }
            </div>
            {timeDetail.map((item)=>{
              if(!item) return;
              return <TimeDetail data={item}/>
            })}
          </div>
        </div>
        <div className='background' onClick={removeDetail}>
              <div className='xmarkContainer' onClick={removeDetail}>
                  <FontAwesomeIcon icon={faXmark} className="navIcons" />
              </div>
        </div>
    </>
  )
}
