import React from 'react'
import "../Style/TimeDetail.scss"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {faBagShopping, faUtensils, faHouse, faMusic, faCar, faMugSaucer, faBicycle, faMountainSun, faCableCar, faSchool, faShip, faTrain  } from '@fortawesome/free-solid-svg-icons'

const iconMap = {
  faBagShopping, faUtensils, faHouse, faMusic, faCar, faMugSaucer, faBicycle, faMountainSun, faCableCar, faSchool, faShip, faTrain
}

export default function TimeDetail({data}) {
  return (
    <div className='timeDetailContainer'>
        <div className='timeLine'></div>
        <div className='timeDetailTitle'>
          <FontAwesomeIcon icon={iconMap[data["type"]]} className="navIcons" />
          <span>{data["name"]} - {data["time"]}</span>
          <div className='timeItemDetail'>
            {data["content"]}
          </div>
        </div>
    </div>
  )
}
